.offer-section {
    position: relative;
    &::before {
        background-image: url(../../img/bg-m2.png);
        position: absolute;
        height: 100%;
        width: 100%;
        background-repeat: no-repeat;
        background-size: contain;
        content: "";
        left: 0;
        right: 0;
        background-position: center;
    }
    .title-main {
        margin-bottom: 70px;
        @media (max-width: 991.5px) {
            margin-bottom: 30px;
        }
    }
    .item-data {
        margin-bottom: 30px;
        .items {
            border: 1px solid $gray-light;
            text-align: center;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            padding: 60px 15px;
            transition: all .5s ease 0s;
            @media (max-width: 991.5px) {
                padding: 30px 15px;
            }
            .item {
                .img {
                    height: 120px;
                    width: 120px;
                    border-radius: 50%;
                    background-color: $blue;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin: 0 auto 25px;
                    transition: all .5s ease 0s;
                    img {
                        max-width: 52px;
                    }
                }
                h3 {
                    font-size: 18px;
                    line-height: 32px;
                    color: #341c77;
                    font-weight: 700;
                    font-family: "Averia Sans Libre";
                    text-align: center;
                }
            }
            &:hover {
                -webkit-box-shadow: 1px -1px 8px 3px rgba(211, 211, 211, 0.88);
                box-shadow: 1px -1px 8px 3px rgba(211, 211, 211, 0.88);
                transition: all .5s ease 0s;
                .item {
                    .img {
                        background-color: $orange;
                        transition: all .5s ease 0s;
                    }
                }
            }
        }
    }
    .row {
        margin-bottom: 70px;
    }
    .welcome {
        .text-content {
            text-align: left;
            .title-main {
                margin-bottom: 30px;
            }
        }
    }
    p {
        font-size: 22px;
        text-align: left;
        margin-bottom: 50px;
        @media (max-width: 991.5px) {
            font-size: 18px;
        }
    }
}

.our-program {
    position: relative;
    padding: 100px 0;
    @media (max-width: 991.5px) {
        padding: 50px 0;
    }
    &::before {
        background-image: url(../../img/Base.png);
        position: absolute;
        height: 100%;
        width: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        content: "";
        top: 0;
        left: 0;
        right: 0;
        background-position: center;
    }
    .title-main {
        margin-bottom: 60px;
    }
    .program-inner {
        .item-blog {
            border-radius: 8px;
            overflow: hidden;
            background-color: $white;
            transition: all .5s ease 0s;
            margin-bottom: 30px;
            .img-wrap {
                img {
                    width: 100%;
                }
            }
            .text-content {
                padding: 50px;
                @media (max-width: 991.5px) {
                    padding: 30px;
                }
                h4 {
                    color: $blue;
                    margin-bottom: 25px;
                }
                .details {
                    ul {
                        padding-left: 0;
                        li {
                            list-style-type: none;
                            margin-bottom: 10px;
                            font-family: $secondary-font;
                            color: $blue;
                            font-size: 16px;
                            display: flex;
                            align-items: center;
                            img {
                                margin-right: 10px;
                                max-width: 16px;
                            }
                        }
                    }
                }
            }
            .divider {
                margin: 20px 0;
                border: .5px solid $gray-light;
            }
            .button-wrap {
                .btn {
                    height: 50px;
                    width: 100%;
                    border-radius: 0;
                    border: none;
                    font-size: 18px;
                }
            }
            &:hover {
                -webkit-box-shadow: 1px -1px 8px 3px rgba(211, 211, 211, 0.88);
                box-shadow: 1px -1px 8px 3px rgba(211, 211, 211, 0.88);
                transition: all .5s ease 0s;
                h4 {
                    color: $orange;
                }
                .btn {
                    background-color: $blue;
                }
            }
        }
    }
}

.news-articles {
    position: relative;
    padding: 100px 0;
    margin-bottom: 0;
    @media (max-width: 991.5px) {
        padding: 50px 0;
    }
    &::before {
        background-image: url(../../img/Base.png);
        position: absolute;
        height: 100%;
        width: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        content: "";
        top: 0;
        left: 0;
        right: 0;
        background-position: center;
    }
    .title-main {
        margin-bottom: 50px;
    }
    .article {
        transition: all .5s ease 0s;
        img {
            transition: all .5s ease 0s;
            border-radius: 10px;
            width: 100%;
        }
        .details {
            margin: 25px 0;
            .sub-title {
                font-size: 16px;
                line-height: 30px;
                color: $orange;
                margin-bottom: 10px;
            }
            h3 {
                font-size: 24;
                line-height: 30px;
                color: $blue;
            }
            p {
                font-size: 16px;
                line-height: 30px;
                color: $gray-2;
            }
        }
        .read-more {
            a {
                color: $blue;
                font-size: 25px;
            }
        }
        &:hover {
            img {
                -webkit-box-shadow: 1px -1px 8px 3px rgba(211, 211, 211, 0.88);
                box-shadow: 1px -1px 8px 3px rgba(211, 211, 211, 0.88);
                transition: all .5s ease 0s;
            }
        }
    }
}

.view-program {
    margin-bottom: 0;
    background-color: $orange;
    padding: 100px 0;
    @media (max-width: 991.5px) {
        padding: 50px 0;
    }
    .title-main {
        color: $white;
        padding-top: 0;
        text-align: left;
        @media (max-width: 991.5px) {
            margin-bottom: 30px;
        }
    }
    .btn-wrap {
        text-align: right;
        @media (max-width: 991.5px) {
            text-align: left;
        }
    }
}

.our-teachers {
    padding: 100px 0 100px;
    position: relative;
    @media (max-width: 991.5px) {
        padding: 50px 0;
    }
    &::before {
        background-image: url(../../img/bg-m1.png);
        position: absolute;
        height: 100%;
        width: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        content: "";
        top: 0;
        left: 0;
        right: 0;
        background-position: bottom;
    }
    .title-main {
        margin-bottom: 50px;
    }
    .teacher {
        display: flex;
        margin-bottom: 30px;
        border: 1px solid $gray-light;
        border-radius: 8px;
        transition: all .5s ease 0s;
        @media (max-width: 991.5px) {
            flex-direction: column;
        }
        .left {
            width: 60%;
            @media (max-width: 991.5px) {
                width: 100%;
            }
            img {
                height: 100%;
                background-size: cover;
                width: 100%;
                object-fit: cover;
            }
        }
        .right {
            .detail {
                padding: 50px;
                @media (max-width: 991.5px) {
                    padding: 30px;
                }
            }
        }
        .social {
            ul {
                padding-left: 0;
                display: flex;
                background-color: $orange;
                padding: 15px 50px;
                margin-bottom: 0;
                transition: all .5s ease 0s;
                li {
                    list-style-type: none;
                    margin-right: 30px;
                    a {
                        color: $white;
                    }
                }
            }
        }
        &:hover {
            -webkit-box-shadow: 1px -1px 8px 3px rgba(211, 211, 211, 0.88);
            box-shadow: 1px -1px 8px 3px rgba(211, 211, 211, 0.88);
            transition: all .5s ease 0s;
            .social {
                ul {
                    background-color: $blue;
                    transition: all .5s ease 0s;
                    li {
                        a {
                            &:hover {
                                color: $orange;
                                transition: all .5s ease 0s;
                            }
                        }
                    }
                }
            }
        }
    }
}

.gallary-item-scroll {
    overflow: hidden;
    margin-bottom: 0;
    .items {
        .item {
            display: inline-block;
            img {
                width: 100%;
            }
        }
    }
}

footer {
    padding: 130px 0 50px;
    position: relative;
    color: $white;
    font-family: $secondary-font;
    &::before {
        background-image: url(../../img/footer.png);
        position: absolute;
        height: 100%;
        width: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        content: "";
        top: 0;
        left: 0;
        right: 0;
        background-position: top;
    }
    .title {
        font-family: $primary-font;
        font-size: 24px;
        font-weight: 400;
        margin-bottom: 40px;
        @media (max-width: 767px) {
            margin-bottom: 20px;
        }
    }
    .footer-links {
        p {
            margin-bottom: 40px;
            font-weight: 100;
        }
    }
    .activites.border-link {
        ul {
            padding-left: 0;
            li {
                list-style-type: none;
                border: 1px solid rgba(255, 255, 255, 0.1);
                margin-bottom: 8px;
                border-radius: 6px;
                transition: all .5s ease 0s;
                max-width: 200px;
                a {
                    color: $white;
                    padding: 10px;
                    display: inline-block;
                    width: 100%;
                    text-decoration: none;
                }
                &:hover {
                    border: 1px solid $orange;
                    transition: all .5s ease 0s;
                }
            }
        }
    }
    .activites {
        ul {
            padding-left: 20px;
            li {
                margin-bottom: 6px;
                transition: all .5s ease 0s;
                &::marker {
                    color: $orange;
                }
                a {
                    color: $white;
                    display: inline-block;
                    width: 100%;
                    text-decoration: none;
                }
            }
        }
    }
    .contact {
        ul {
            padding-left: 0;
            li {
                list-style-type: none;
                margin-bottom: 20px;
                padding-left: 30px;
                position: relative;
                a,
                span {
                    color: $white;
                }
                img {
                    margin-right: 10px;
                    position: absolute;
                    left: 0;
                    top: 2px;
                }
            }
        }
    }
    .bottom-footer {
        position: relative;
        display: flex;
        justify-content: space-between;
        padding-top: 50px;
        margin-top: 50px;
        border-top: 1px solid hsla(0, 0%, 100%, .1);
        @media (max-width: 991.5px) {
            flex-direction: column;
        }
        @media (max-width: 767px) {
            margin-top: 30px;
            padding-top: 30px;
        }
        .copy-right {
            color: $white;
            margin-bottom: 10px;
            p {
                margin-bottom: 0;
            }
        }
    }
}

.email-section {
    margin-bottom: 0;
    padding: 100px 0;
    @media (max-width: 991.5px) {
        padding: 50px 0;
    }
    h3 {
        font-size: 30px;
        color: $blue;
    }
    .input-btn {
        display: flex;
        @media (max-width: 767px) {
            flex-direction: column;
        }
        .btn,
        input {
            height: 75px;
        }
        .btn {
            @media (max-width: 767px) {
                width: 100%;
                margin-top: 20px;
            }
        }
        input {
            background-color: $light;
            border: none;
            margin-right: 10px;
        }
    }
}

.testimonial {
    padding: 100px 0;
    position: relative;
    color: $white;
    @media (max-width: 991.5px) {
        padding: 50px 0;
    }
    &::before {
        background-image: url(../../img/bg-blue.png);
        position: absolute;
        height: 100%;
        width: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        content: "";
        top: 0;
        left: 0;
        right: 0;
        background-position: top;
    }
    .title-main {
        color: $white;
        margin-bottom: 50px;
    }
    .testimonial-slider {
        position: relative;
        max-width: 800px;
        margin: 0 auto;
        &::before {
            background-image: url(../../img/testimonial.png);
            position: absolute;
            height: 100%;
            width: 100%;
            background-repeat: no-repeat;
            background-size: 100% 100%;
            content: "";
            top: 0;
            left: 0;
            right: 0;
            background-position: top;
            @media (max-width: 991.5px) {
                display: none;
            }
        }
        .slick-list {
            overflow: hidden;
        }
        .item {
            padding: 100px 100px 180px;
            position: relative;
            color: $blue;
            text-align: center;
            font-family: $secondary-font;
            display: inline-block;
            max-width: calc(100% - 40px);
            @media (max-width: 991.5px) {
                padding: 100px 15px;
                color: $white;
            }
            @media (max-width: 767px) {
                padding: 60px 15px;
                color: $white;
            }
            &::before {
                content: "";
                background-image: url(../../img/quote.png);
                position: absolute;
                top: 39px;
                z-index: 22;
                height: 30px;
                width: 30px;
                left: 53px;
                background-size: contain;
                background-repeat: no-repeat;
                @media (max-width: 991.5px) {
                    top: 0;
                }
            }
            &::after {
                content: "";
                background-image: url(../../img/quote.png);
                position: absolute;
                z-index: 22;
                height: 30px;
                width: 30px;
                background-size: contain;
                background-repeat: no-repeat;
                right: 45px;
                bottom: 130px;
                transform: rotate( 180deg);
                @media (max-width: 991.5px) {
                    bottom: 0;
                }
            }
            h3 {
                font-size: 25px;
                color: $gray-2;
                font-weight: 300;
                text-align: center;
                margin-bottom: 20px;
                @media (max-width: 991.5px) {
                    color: $white;
                }
            }
            .sub-heading {
                font-weight: bold;
            }
            p {
                color: $gray-2;
                @media (max-width: 991.5px) {
                    color: $white;
                    font-size: 22px;
                }
            }
            .round-img {
                height: 50px;
                width: 50px;
                background-color: $blue;
                border-radius: 50%;
                margin: 0 auto;
            }
        }
    }
}

.banner-header {
    .item-slider {
        overflow: hidden;
        position: relative;
        .item {
            position: relative;
            display: inline-block;
            img {
                width: 100%;
                @media (max-width: 991.5px) {
                    height: 70vh;
                    background-size: cover;
                    object-fit: cover;
                }
            }
            .text-content {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                text-align: center;
                max-width: 800px;
                width: 100%;
                padding: 20px;
                display: inline-block;
                .banner-title {
                    margin-bottom: 40px;
                    font-size: 100px;
                    line-height: 90px;
                    color: $white;
                    font-weight: 700;
                    position: relative;
                    padding: 0 50px;
                    &::before {
                        content: "";
                        background-image: url(../../img/star.png);
                        height: 50px;
                        width: 50px;
                        position: absolute;
                        left: 0;
                        top: 20px;
                        background-size: contain;
                        background-repeat: no-repeat;
                    }
                    &::after {
                        content: "";
                        background-image: url(../../img/star.png);
                        height: 50px;
                        width: 50px;
                        position: absolute;
                        right: 0;
                        top: 20px;
                        transform: rotate(60deg);
                        background-size: contain;
                        background-repeat: no-repeat;
                    }
                    @media (max-width: 1199px) {
                        font-size: 60px;
                        line-height: 70px;
                    }
                    @media (max-width: 991.5px) {
                        font-size: 40px;
                        line-height: 50px;
                    }
                    @media (max-width: 767px) {
                        font-size: 28px;
                        line-height: 34px;
                        margin-bottom: 20px;
                    }
                }
                .banner-sub {
                    margin-bottom: 20px;
                    font-size: 30px;
                    color: $white;
                    font-weight: 400;
                    @media (max-width: 1199px) {
                        font-size: 18px;
                    }
                }
                .btn {
                    @media (max-width: 991.5px) {
                        height: 50px;
                    }
                }
            }
        }
        .slick-dots {
            bottom: 0;
            @media (min-width: 1200px) {
                bottom: 30px;
            }
            li {
                button {
                    height: 20px;
                    width: 20px;
                }
            }
        }
        .arrow-banner {
            position: absolute;
            z-index: 2;
            justify-content: space-between;
            width: calc(100% - 60px);
            display: flex;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            @media (max-width: 991.5px) {
                display: none;
            }
            i {
                height: 60px;
                width: 60px;
                color: $white;
                font-size: 25px;
                border-radius: 50%;
                border: 1px solid $white;
                display: flex;
                align-items: center;
                justify-content: center;
                opacity: .4;
                &:hover {
                    opacity: 1;
                }
            }
        }
    }
}

//slick dots design
.slick-dots {
    padding-left: 0;
    display: flex;
    align-items: center;
    position: absolute;
    justify-content: center;
    width: 100%;
    bottom: -50px;
    li {
        list-style-type: none;
        margin-right: 5px;
        button {
            font-size: 0;
            height: 24px;
            width: 24px;
            border-radius: 50%;
            background-color: $white;
            -webkit-appearance: none;
            border: none;
            opacity: .6;
            &::before {
                content: '';
                height: 6px;
                width: 6px;
                display: inline-block;
                border-radius: 50%;
                background-color: $blue;
            }
        }
        &.slick-active {
            button {
                opacity: 1;
            }
        }
    }
}