body {
    margin: 0;
    padding: 0;
    font-family: $primary-font;
}

p {
    font-family: $secondary-font;
}

img {
    height: auto;
    max-width: 100%;
}

* {
    box-sizing: border-box;
}

.title-main {
    font-size: 50px;
    color: $blue;
    font-weight: 700;
    font-family: $primary-font;
    text-align: center;
    position: relative;
    padding-top: 25px;
    @media (max-width: 991px) {
        font-size: 30px;
    }
    &.left-align {
        text-align: left;
        &::before {
            left: 0;
            transform: unset;
        }
    }
    &::before {
        content: '';
        height: 4px;
        background-color: $orange;
        width: 30px;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 0;
    }
}

section {
    margin-bottom: 100px;
    @media (max-width: 991px) {
        margin-bottom: 50px;
    }
}

.social {
    margin-bottom: 20px;
    ul {
        padding-left: 0;
        display: flex;
        align-items: center;
        li {
            list-style-type: none;
            margin-right: 15px;
            a,
            i {
                color: $white;
                transition: all .5s ease 0s;
            }
            a {
                &:hover {
                    i {
                        color: $orange;
                        transition: all .5s ease 0s;
                    }
                }
            }
        }
    }
}