//header
header {
    .upper-header {
        background-color: $blue;
        padding: 15px;
        @media (max-width: 767.5px) {
            display: none;
        }
        ul {
            padding-left: 200px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 0;
            @media (max-width: 991.5px) {
                padding-left: 0;
            }
            @media (max-width: 767.5px) {
                flex-direction: column;
            }
            li {
                list-style-type: none;
                color: $white;
                font-family: $secondary-font;
                @media (max-width: 991.5px) {
                    font-size: 14px;
                }
                @media (max-width: 767.5px) {
                    margin-bottom: 15px;
                    &:last-child {
                        margin-bottom: 0;
                    }
                }
                a,
                span {
                    color: $white;
                    font-family: $secondary-font;
                    text-decoration: none;
                    transition: all .5s ease 0s;
                    &:hover {
                        color: $orange;
                        transition: all .5s ease 0s;
                    }
                    @media (max-width: 991.5px) {
                        font-size: 14px;
                    }
                }
                img {
                    margin-right: 10px;
                }
            }
            ul {
                padding-left: 0;
                li {
                    margin-right: 10px;
                }
            }
        }
    }
    .navbar {
        min-height: 100px;
        padding-right: 0;
        @media (max-width: 991.5px) {
            min-height: 60px;
        }
        .navbar-brand {
            position: absolute;
            height: 200px;
            width: 200px;
            z-index: 1;
            @media (max-width: 991.5px) {
                position: relative;
                height: 70px;
                width: 70px;
            }
        }
        .navbar-nav {
            padding-left: 250px;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            @media (max-width: 991.5px) {
                padding-left: 0;
                padding-bottom: 50px;
            }
            .social-icon {
                margin-right: 100px;
                @media (max-width: 991.5px) {
                    margin-right: 0;
                    position: absolute;
                    bottom: 20px;
                }
                .social {
                    margin-bottom: 0;
                    ul {
                        li {
                            a,
                            i {
                                color: $orange;
                                &:hover {
                                    color: $blue;
                                }
                            }
                            &:nth-child(2n) {
                                color: $blue;
                                &:hover {
                                    color: $orange;
                                }
                            }
                        }
                    }
                }
            }
            .nav-item {
                .nav-link {
                    color: $blue;
                    font-family: $secondary-font;
                }
                &.active {
                    .nav-link {
                        font-weight: bold;
                        @media (max-width: 991.5px) {
                            text-align: center;
                        }
                    }
                }
            }
        }
        .navbar-toggler-icon {
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
}

// for dropdown hover and click
@media (min-width: 992px) {
    li.dropdown {
        &:hover {
            ul.dropdown-menu {
                display: block;
            }
        }
        .dropdown-toggle {
            &::after {
                display: none;
            }
        }
        a {
            color: $white;
        }
        .dropdown-menu {
            background-color: $orange;
        }
    }
}

.dropdown-menu {
    @media (max-width: 991.5px) {
        text-align: center;
    }
}